/* navbar css*/
.navbar{
    position:fixed;
    top: 0;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    z-index: 10;
    height:10vh;
    /* background-color: var(--main-black-color); */
    
}
.navbar a{
    padding:1em 1em;
    text-decoration: none;
    color:white;
}
.name{
    font-weight: 800;
    padding:2em;
}
.underline{
    animation: disapear 1.6s infinite;
    animation-timing-function:step-start;
}
@keyframes disapear{
    0%{
        fill:white;
    }
    50%{
        fill:black;
    }
    100%{
        fill:white;
    }
}
.decor{
    color:var(--main-white-color);
}
.labels{
    display: flex;
    justify-content: space-between;
}
.label{
    padding:1em 1em;
    text-decoration: none;
    color:white;
    font-weight: 600;
}
ul{
    list-style: none;
    display:flex;
}

.label .active{
    border-bottom: 2px solid white;
}
.label .contactbtn{
    border:2px solid white;
    border-radius: 5px;
    padding:0.6em 0.8em;
}
.label .contactbtn:hover{
    background-color: var(--main-white-color);
    color: var(--main-black-color);
}

li{
    cursor:pointer;
}
/* landing css*/
.container{
    width:100%;
    /* scroll-snap-type: y mandatory;
    overflow-y: scroll; */
}
.landingContainer{
    display: flex;
    position: relative;
}

.titleText{
    width: 50%; 
}
.title{
    font-size: 2.8em;  
    font-weight: 800;
}
.subTitle{
    font-weight:600;
    color:var(--main-grey-color);
    padding:10px 0;
}

.text{
    padding:30px 0;
    font-weight: 300;
    font-size: 1em;
}
.updown{
    animation: ud 3s infinite;
    animation-timing-function:ease-in-out
}
@keyframes ud{
    0%{
        transform: translate(0,5px);
    }
    50%{
        transform: translate(0,0px);
    }
    100%{
        transform: translate(0,5px);
    }
}
.icon{
    display: block;
    box-sizing: border-box;
}
.icon p{
    margin-top:1px;
}
.icons a{
    padding:0 6px;
}
.icons{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
/* skills */
.skills{
    text-align: center;
    display: flex;
    flex-direction: column;
    padding:5%;
}
.techStack{
    font-weight:400;
    width: 60%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content:center;
    align-items: center;
    gap:50px;
    margin:5% auto;
}
.techStack div{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:5px;
}

.skills p{
    margin:auto 15%;
}
/* aboutme */
.aboutMe{
    display: flex;
    justify-content:flex-start;
    gap:20px;
    padding:auto 20%;
}
.section{
    position: relative;
    margin:auto;
    margin-top:10vh;
    height:90vh;
    justify-content:center;
    align-items: center;
    padding:auto;


    /* scroll-snap-align:center; */
    transition: opacity 2s ease-in;
    animation: fadeInA 1s linear;
    /* animation: fadeInA ease 1s; */
}
@keyframes fadeInA {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
    
}

.selfImg{
    max-width:500px;
    width:30%;
}
.selfIntro{
    padding:auto;
    width:50%;
}
/* projectcss */
.sections{
    color:var(--main-grey-color);
    font-weight:800;
    font-size: 1.5em;

}
.projects{
    width:100%;
    margin:auto;
}
.projectText{
    width: 60%;
    font-size: 0.4em;
    font-weight:300;
    text-align: center;
    line-height: 1.4em;;
}
.seeB{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.seebelow{
    background: linear-gradient(120deg,#BB64C6,#FF8E26);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    width: fit-content;
    font-weight:600;
}
.projectTitle{
    text-align: center;
}
.projects img{
    width:55%;
    border-radius: 10px;
}
.project{
    display: flex;
    justify-content:center;
    align-items: center;
    margin:auto;
    margin-top:8%;

    gap:40px;
}
.project img{
    width:40%;
}

.projectInfo{
    width:30%
}

.projectName{
    font-size: 2.4em;
    font-weight: 800;
    text-transform: capitalize;
    line-height: 1.4em;
    padding: 10px 0px;
}
.logo img{
    width:180px;
    object-fit:contain;

}
.logo{
    display: flex;
    justify-content:flex-start;
    padding: 10px 0px;
}
.projectDescription{
    line-height: 1.4em;
    font-weight: 300;
    color:var(--main-white-color);
    padding: 10px 0px;
}
.buttons{
    display: flex;
    justify-content:flex-start;
    gap:50px;
    margin:40px auto;
}
.buttons a{
    text-decoration: none;
    display: flex;
    justify-content:flex-start;
    align-items: center;
    color: var(--main-white-color);
    font-weight: 600;

}
.buttons a span{
    padding-right:5px;

}

/* .buttons a:hover{
    transform: translateX(10px);

}  */
.tech{
    font-weight: 700;
    display: flex;
    justify-content:flex-start;
    gap: 20px;
}
.sectionName{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.tab{
    display: flex;
    justify-content: flex-start;
    margin:2% auto;
    margin-left:0;
    font-size: 1.2em;
}
.option{
    margin:1em;
    border-bottom: 2px solid white;
    padding-bottom: 3px;
}
.option a{
    color: var(--main-white-color);
    text-decoration: none;
}
.option a:hover{
    background-color: var(--main-white-color);
    color: var(--main-black-color);
}

.projectsTitle{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap:5%;
}

